.popular-tovars{
	padding: 25px 0 24px;
	background: #13387d;
	@nest &__more{
		font-size: 0;
		margin: 23px 0 0;
		text-align: center;
		position: relative;
		@nest &:after{
			position: absolute;
			content: '';
			top: 50%;
			left: 0;
			right: 0;
			height: 1px;
			background: #fff;
			margin-top: -.5px;
			@media (--sm){
				left: -15px;
				right: -15px;
			}
		}
		@nest & a{
			font-size: 14px;
			color: #fff;
			z-index: 10;
			width: 100%;
			font-weight: 800;
			max-width: 275px;
			border-radius: 10px;
			background: #13387d;
			position: relative;
			overflow: hidden;
			padding: 15px 0 13px;
			text-decoration: none;
			display: inline-block;
			border: 1px solid #fff;
			text-transform: uppercase;
			@media (--sm){
				font-size: 14px;
				width: 100%;
				display: block;
				margin: auto;
				max-width: 212px;
				padding-left: 0;
				padding-right: 0;
			}
			@nest &:before{
				position: absolute;
				content: '';
				top: 0;
				left: 50%;
				width: 0;
				height: 100%;
				z-index: -1;
				background: #f51610;
				transition: width 300ms;
			}
			@nest &:after{
				position: absolute;
				content: '';
				top: 0;
				right: 50%;
				width: 0;
				height: 100%;
				z-index: -1;
				background: #f51610;
				transition: width 300ms;
			}
			@nest &:hover{
				@nest &:before{
					width: 50%;
				}
				@nest &:after{
					width: 50%;
				}
			}
		}
		@nest &--blue{
			margin-top: 12px;
			@nest &:after{
				background: #2950a0;
			}
			@nest & a{
				border: 0;
				font-size: 18px;
				font-weight: 400;
				border-radius: 12px;
				padding: 12px 0 11px;
				font-family: var(--font);
				text-transform: none;
			}
		}
	}
}