.reviews-block{
	clear: fix;
	border-radius: 10px;
	border: 1px solid #9ab4e5;
	padding: 18px 34px 22px 37px;
	@media (--xl){
		padding-right: 17px;
		padding-left: 23px;
	}
	@media (--lg){
		padding-left: 22px;
		padding-right: 22px;
	}
	@nest &__col{
		display: table-cell;
		@nest &--sm{
			width: 26%;
			vertical-align: top;
			@media (--lg){
				width: 100%;
				display: block;
			}
		}
		@nest &--lg{
			width: 74%;
			vertical-align: middle;
			@media (--xl){
				padding-left: 18px;
			}
			@media (--lg){
				width: 100%;
				display: block;
				padding-left: 0;
			}
		}
	}
	@nest & img{
		display: block;
	}
	@nest &__header{
		display: table;
		width: 100%;
		@nest & p{
			font-size: 14px;
			color: #656565;
			margin-bottom: 0;
			line-height: 18px;
			font-family: var(--font);
			@media (--xl){
				font-size: 12px;
				letter-spacing: -.1px;
			}
			@media (--lg){
				margin-top: 8px;
				text-align: center;
				letter-spacing: 0;
			}
			@nest & b{
				font-weight: 700;
			}
		}
	}
	@nest &__user{
		width: 144px;
		text-align: center;
		margin: -67px 0 0 -38px;
		@media (--xl){
			margin-left: -23px;
		}
		@media (--lg){
			margin: -67px auto 0;
		}
		@nest & img{
			width: 144px;
			height: 144px;
			border-radius: 50%;
		}
		@nest & span{
			font-size: 18px;
			color: #2950a0;
			display: block;
			margin-top: 3px;
			line-height: normal;
			font-family: var(--font);
			@media (--lg){
				margin-top: 4px;
			}
		}
		@nest & i{
			font-size: 12px;
			color: #656565;
			display: block;
			margin-top: 2px;
			font-style: normal;
			line-height: normal;
			font-family: var(--font);
			@media (--xl){
				font-size: 10px;
				margin-top: 5px;
			}
			@media (--lg){
				margin-top: 1px;
			}
		}
	}
	@nest &__footer{
		display: table;
		width: 100%;
		margin: 22px 0 0;
		padding: 23px 0 0;
		border-top: 1px solid #9ab4e5;
		@media (--xl){
			margin-top: 23px;
		}
		@media (--lg){
			margin-top: 5px;
			text-align: center;
		}
		@nest & img{
			max-width: 60px;
			@media (--xl){
				margin-left: 19px;
			}
			@media (--lg){
				margin-left: 0;
			}
		}
		@nest & span{
			font-size: 14px;
			color: #000;
			display: block;
			line-height: normal;
			@media (--xl){
				font-size: 12px;
				line-height: 19px;
			}
		}
		@nest & a{
			font-size: 14px;
			color: #2557bf;
			margin-top: 7px;
			line-height: normal;
			text-decoration: none;
			display: inline-block;
			transition: border-width 300ms;
			border-bottom: 1px solid #3c65c3;
			@nest &:hover{
				border-bottom: 0;
			}
			@media (--xl){
				font-size: 12px;
				margin-top: 0;
			}
		}
	}
	@nest &__footer &{
		@nest &__col{
			@nest &--sm{
				@media (--lg){
					width: auto;
					display: inline-block;
					vertical-align: middle;
				}
			}
			@nest &--lg{
				@media (--lg){
					width: auto;
					text-align: left;
					display: inline-block;
					vertical-align: middle;
				}
			}
		}
	}
	@nest &__rating{
		width: 100%;
		max-width: 184px;
		display: inline-block;
		@media (--xl){
			max-width: 122px;
			margin-left: 12px;
		}
		@media (--lg){
			margin-left: 21px;
		}
	}
}