.section-title{
	font-size: 24px;
	color: #282828;
	font-weight: 700;
	text-align: center;
	margin-bottom: 0;
	font-family: var(--font);
	@media (--sm){
		font-size: 18px;
		max-width: 235px;
		margin: auto;
		line-height: 18px;
	}
	@nest &--white{
		color: #fff;
		font-weight: 800;
		font-family: inherit;
		@media (--sm){
			line-height: 24px;
		}
	}
	@nest &--blue{
		color: #2950a0;
		font-family: var(--font);
	}
	@nest &--line{
		position: relative;
		@nest &:before{
			position: absolute;
			content: '';
			top: 50%;
			left: 0;
			width: 35%;
			height: 1px;
			background: #2950a0;
			@media (--xl){
				width: 30%;
			}
			@media (--lg){
				display: none;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			top: 50%;
			right: 0;
			width: 35%;
			height: 1px;
			background: #2950a0;
			@media (--xl){
				width: 30%;
			}
			@media (--lg){
				display: none;
			}
		}
	}
}