.card-block{
	max-width: 265px;
	margin: 21px auto 0;
	border-radius: 14px;
	background: #fff;
	overflow: hidden;
	padding: 20px 10px;
	text-align: center;
	@media (--sm){
		max-width: 212px;
		margin-top: 24px;
	}
	@nest &--catalogue{
		margin-bottom: 45px;
		border: 1px solid #9ab4e5;
		transition: box-shadow 300ms;
		@nest &.active{
			box-shadow: 4px 6.928px 8px 0px rgba(41, 80, 160, 0.25);
		}
		@media (--lg){
			margin-bottom: 10px;
		}
	}
	@nest &__image{
		position: relative;
		@nest &:after{
			display: inline-block;
			content: '';
			height: 100%;
			vertical-align: middle;
		}
		@nest & a{
			text-decoration: none;
		}
		@nest & img{
			display: inline-block;
			vertical-align: middle;
		}
	}
	@nest &__title{
		width: 100%;
		display: table;
		margin: 13px 0 0;
		@media (--xl){
			margin-top: 3px;
		}
		@media (--sm){
			margin-top: 4px;
		}
		@nest & a{
			font-size: 18px;
			color: #2950a0;
			margin-bottom: 0;
			font-family: var(--font);
			display: table-cell;
			text-decoration: none;
			vertical-align: middle;
			@media (--xl){
				line-height: 20px;
			}
		}
	}
	@nest &__info{
		max-width: 150px;
		margin: 10px auto 0;
		text-align: left;
		@nest & p{
			font-size: 12px;
			color: #606060;
			margin-bottom: 0;
			font-family: var(--font);
			@nest & b{
				font-weight: 700;
			}
		}
	}
	@nest & span{
		font-size: 18px;
		color: #282828;
		display: block;
		font-weight: 900;
		margin-top: 18px;
		font-family: var(--font);
		@media (--xl){
			margin-top: 14px;
		}
		@media (--sm){
			margin-top: 11px;
		}
		@nest & i{
			font-weight: 400;
			font-style: normal;
			line-height: normal;
			text-decoration: line-through;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #282828;
		height: 35px;
		width: 123px;
		border: 0;
		padding: 0;
		z-index: 10;
		display: block;
		outline: none;
		margin: 3px auto 0;
		border-radius: 5px;
		overflow: hidden;
		position: relative;
		background: #ffc700;
		transition: color 300ms;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: 0;
			height: 0;
			z-index: -1;
			background: #f51610;
			transition: height 300ms;
		}
		@nest &:hover{
			color: #fff;
			@nest &:after{
				height: 100%;
			}
		}
		@nest &--bg{
			color: #fff;
			font-weight: 700;
			margin-top: 10px;
			background: #f51610;
			font-family: var(--font);
			transition: box-shadow 300ms, color 300ms;
			@nest &:hover{
				background: #f51610;
				box-shadow: 2.5px 4.33px 5px 0px rgba(0, 0, 0, 0.3);
			}
		}
	}
	@nest &__more{
		font-size: 12px;
		color: #7599e2;
		margin-top: 10px;
		display: inline-block;
		font-family: var(--font);
		@nest &:hover{
			color: #f51610;
			text-decoration: none;
		}
	}
}