.header{
	background: #fff;
	padding: 12px 0 10px;
	position: relative;
	@media (--lg){
		text-align: center;
	}
	@media (--sm){
		padding: 8px 0 15px;
	}
	@nest &__item{
		@media (--sm){
			clear: fix;
		}
		@nest &--indent{
			@media (--from-lg) and (--xl){
				float: left;
			}
			@media (--lg){
				margin-bottom: 20px;
			}
			@media (--sm){
				margin-bottom: 9px;
				padding-bottom: 6px;
				border-bottom: 1px solid #9ab4e5;
			}
		}
	}
	@nest &__logo{
		font-size: 0;
		float: left;
		margin-top: 6px;
		@media (--lg){
			float: none;
			margin-top: 0;
			display: inline-block;
			vertical-align: middle;
		}
		@nest & a{
			display: inline-block;
			vertical-align: middle;
			@nest & img{
				height: 28.5px;
				@media (--sm){
					height: auto;
				}
			}
		}
		@nest & span{
			font-size: 12px;
			color: #000;
			max-width: 120px;
			margin: 0 0 0 27px;
			padding: 7px 0 5px 12px;
			display: inline-block;
			line-height: normal;
			vertical-align: middle;
			border-left: 1px solid #9ab4e5;
			@media (--xl){
				margin-left: 12px;
			}
			@media (--sm){
				margin: 0;
				padding: 0;
				font-size: 10px;
				font-weight: 800;
				max-width: 100%;
				display: block;
				border-left: 0;
				margin-top: 3px;
			}
		}
	}
	@nest &__info{
		width: 100%;
		max-width: 195px;
		float: right;
		margin-top: 14px;
		padding-left: 30px;
		position: relative;
		@media (--xl){
			width: auto;
			margin-left: 18px;
		}
		@media (--lg){
			float: none;
			margin-top: 0;
			display: inline-block;
			vertical-align: middle;
		}
		@media (--sm){
			display: none;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			margin-top: -10.5px;
			background: resolve('clock.png');
			width: width('clock.png');
			height: height('clock.png');
		}
		@nest & span{
			font-size: 12px;
			color: #1a49a0;
			font-weight: 800;
			display: block;
			line-height: normal;
			text-transform: uppercase;
			@media (--xl){
				font-size: 10px;
			}
		}
	}
	@nest &__contacts{
		display: inline-block;
		font-family: var(--font);
		@media (--xl){
			margin-top: 3px;
		}
		@media (--lg){
			margin-top: 0;
			vertical-align: middle;
		}
		@media (--sm){
			display: none;
		}
		@nest &--indent{
			margin-right: 5px;
			margin-left: 19px;
			padding-right: 10px;
			position: relative;
			text-align: right;
			@media (--xl){
				margin-left: 26px;
				padding-right: 12px;
				text-align: left;
			}
			@media (--sm){
				display: block;
				float: left;
				margin: 0;
				padding-right: 0;
			}
			@nest &:after{
				position: absolute;
				content: '';
				right: 0;
				top: 50%;
				width: 1px;
				height: 36px;
				margin-top: -18px;
				background: #a4a2ce;
				@media (--sm){
					display: none;
				}
			}
		}
		@nest &--indent &{
			@nest &-tel{
				position: relative;
				@media (--sm){
					padding: 2px 0 0 13px;
				}
				@nest &:after{
					position: absolute;
					content: '';
					top: 50%;
					left: -19px;
					transform: translateY(-50%);
					background: resolve('tel.png');
					width: width('tel.png');
					height: height('tel.png');
					@media (--sm){
						left: 0;
						top: 2px;
						transform: translateY(0);
					}
				}
			}
		}
		@nest & span{
			font-size: 12px;
			color: #282828;
			display: block;
			@media (--xl){
				font-size: 10px;
			}
			@nest & .btn-city{
				color: #2950a0;
				border: 0;
				padding: 0;
				outline: none;
				background: transparent;
				text-decoration: none;
				@nest &:hover{
					text-decoration: underline;
				}
			}
		}
		@nest &-tel{
			font-size: 24px;
			color: #000;
			display: block;
			text-decoration: none;
			@media (--xl){
				font-size: 18px;
			}
			@media (--sm){
				font-size: 14px;
			}
		}
	}
	@nest &__btn-modal{
		font-size: 12px;
		color: #000;
		float: right;
		margin-top: 21px;
		border-radius: 5px;
		letter-spacing: .12px;
		text-decoration: none;
		padding: 5px 10px 4px;
		border: 1px solid #a4a2ce;
		transition: background 300ms, border-color 300ms;
		@nest &:hover{
			background: #ffc700;
			border-color: #efa800;
		}
		@media (--xl){
			margin-top: 12px;
			padding: 4px 10px 2px;
		}
		@media (--lg){
			float: none;
			margin-left: 10px;
			display: inline-block;
			vertical-align: middle;
		}
		@media (--sm){
			font-size: 10px;
			float: right;
			margin: 2px 0 0;
			padding-top: 6px;
		}
	}
}