.bread-crumbs{
  margin: 0 15px;
	font-family: var(--font);
}
.bread-crumbs a{
  font-size: 12px;
  color: #000;
  float: left;
  line-height: 42px;
  padding-right: 14px;
  position: relative;
  @nest &:hover{
  	text-decoration: none;
  }
}
.bread-crumbs a:before{
  font-size: 12px;
  color: #000;
  content: ">";
  right: 4px;
  position: absolute;
  line-height: 42px;
}
.bread-crumbs span{
  font-size: 12px;
  line-height: 42px;
  color: #13387d;
  font-weight: 700;
  float: left;
}