.nav{
	height: 25px;
	font-size: 0;
	border-top: 1px solid #9ab4e5;
	border-bottom: 1px solid #e9edf5;
	@media (--xl){
		margin-top: 8px;
	}
	@media (--md){
		position: absolute;
		content: '';
		top: 100%;
		left: 0;
		right: 0;
		border: 0;
		z-index: 20;
		height: auto;
		margin-top: 0;
		background: #2950a0;
	}
}