.filter-list{
	padding: 0;
	width: 100%;
	float: left;
	display: table;
	list-style: none;
	max-width: 800px;
	text-align: center;
	margin: 0 0 0 20px;
	@media (--xl){
		width: auto;
		margin: 0;
		display: block;
	}
	@media (--lg){
		float: none;
		margin: 15px 0;
	}
	@nest &__item{
		width: 1%;
		display: table-cell;
		position: relative;
		white-space: nowrap;
		@nest &:last-child &{
			@nest &__link{
				margin-right: 0;
			}
		}
		@media (--xl){
			width: auto;
			white-space: normal;
			display: inline-block;
		}
		@media (--lg){
			display: block;
			margin-bottom: 5px;
		}
	}
	@nest &__link{
		font-size: 14px;
		color: #7a7a7a;
		margin: 0 19px;
		padding-right: 20px;
		font-family: var(--font);
		position: relative;
		text-decoration: none;
		display: inline-block;
		transition: color 300ms;
		@media (--xl){
			margin: 0 7px 0 0;
			padding-right: 15px;
		}
		@media (--lg){
			margin-right: 0;
		}
		@nest &:after{
			position: absolute;
			content: '';
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('bot-arr.svg');
			width: width('bot-arr.svg');
			height: height('bot-arr.svg');
			-webkit-background-size: cover;
			background-size: cover;
		}
		@nest &.active{
			color: #000;
			@nest &:after{
				background: resolve('bot-arr-act.svg');
				width: width('bot-arr-act.svg');
				height: height('bot-arr-act.svg');
				-webkit-background-size: cover;
				background-size: cover;
			}
		}
	}
}