.filter{
	clear: fix;
	margin-top: 6px;
	padding: 14px 0 10px;
	border: 1px solid #99b4e5;
	@media (--lg){
		text-align: center;
		padding-bottom: 0;
	}
	@nest &__title{
		float: left;
		font-size: 16px;
		color: #000;
		margin: 0 20px;
		font-family: var(--font);
		@media (--lg){
			float: none;
		}
	}
	@nest &__btn{
		float: right;
		font-size: 14px;
		color: #7599e2;
		border: 0;
		outline: none;
		padding: 0 20px;
		position: relative;
		font-family: var(--font);
		background: transparent;
		transition: color 300ms;
		@media (--lg){
			float: none;
			height: 40px;
			border: 1px solid #7599e2;
			border-bottom: 0;
		}
		@nest &:hover{
			color: #f51610;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: -14px;
			width: 1px;
			height: 46px;
			background: #7599e2;
			@media (--lg){
				display: none;
			}
		}
	}
}