.advantages{
	margin: 58px 0 0;
	padding: 23px 0 29px;
	background: #e4eeff;
	@media (--xl){
		margin-top: 56px;
		padding-top: 22px;
	}
	@media (--md){
		margin-top: 2px;
		padding: 38px 0 27px;
	}
	@nest &__inner{
		margin: 23px 0 0;
		@media (--xl){
			margin-top: 25px;
		}
		@media (--md){
			margin-top: 22px;
		}
	}
	@nest &__item{
		text-align: center;
		@media (--xl){
			padding: 0 10px;
		}
		@media (--md){
			padding: 0;
			margin-bottom: 18px;
		}
		@nest & span{
			font-size: 18px;
			color: #2950a0;
			font-weight: 700;
			display: block;
			line-height: 18px;
			font-family: var(--font);
			@media (--md){
				font-size: 14px;
				line-height: 14px;
			}
		}
		@nest & i{
			font-size: 12px;
			color: #282828;
			margin-top: 6px;
			font-style: normal;
			line-height: normal;
			display: inline-block;
			@media (--md){
				font-size: 10px;
				display: block;
				margin-top: 2px;
				line-height: 13px;
			}
		}
	}
	@nest &__image{
		height: 96px;
		margin-bottom: 10px;
		@media (--xl){
			margin-bottom: 9px;
		}
		@media (--md){
			margin-bottom: 11px;
		}
	}
}