.about-image{
	@media (--md){
		margin: auto;
		max-width: 290px;
	}
	@nest &__image{
		@media (--xl){
			padding: 0;
		}
		@media (--md){
			padding: 0 15px;
		}
		@nest & img{
			@media (--xl){
				height: 240px;
				width: 100%;
			}
			@media (--lg){
				height: auto;
			}
			@media (--md){
				width: 80px;
				height: 117px;
			}
		}
	}
	@nest & span{
		font-size: 12px;
		display: block;
		z-index: 10;
		padding: 11px 0;
		margin: 48px 0 0;
		line-height: normal;
		position: relative;
		text-align: center;
		border-radius: 11px;
		border: 1px solid #3f4890;
		@nest &:after{
			position: absolute;
			content: '';
			top: 50%;
			height: 1px;
			background: #8ea8de;
		}
		@media (--lg){
			margin-top: 30px;
		}
		@media (--md){
			font-size: 12px;
			font-weight: 100;
			margin: 0 0 24px;
		}
		@nest & br{
			@media (--lg){
				display: none;
			}
		}
	}
	@nest &__left{
		@nest & span{
			@nest &:first-child{
				@nest &:after{
					width: 50px;
					top: 5px;
					right: -45px;
					transform: rotate(-41deg);
					@media (--xl){
						width: 80px;
						top: 0px;
						right: -70px;
						transform: rotate(-40deg);
					}
					@media (--md){
						width: 60px;
						top: 13px;
						right: -58px;
						transform: rotate(-21deg);
					}
				}
			}
			@nest &:last-child{
				@nest &:after{
					width: 40px;
					top: 13px;
					right: -37px;
					transform: rotate(-31deg);
					@media (--md){
						top: 14px;
						transform: rotate(-28deg);
					}
				}
			}
		}
	}
	@nest &__right{
		@nest & span{
			@nest &:first-child{
				@media (--md){
					font-size: 10px;
					padding: 7px 0px 6px;
				}
				@nest &:after{
					width: 104px;
					top: 43px;
					left: -100px;
					transform: rotate(-23deg);
					@media (--xl){
						width: 125px;
						top: 48px;
						left: -120px;
					}
					@media (--md){
						width: 85px;
						top: 42px;
						left: -80px;
						transform: rotate(-28deg);
					}
				}
			}
			@nest &:last-child{
				@nest &:after{
					width: 60px;
					top: 1px;
					left: -47px;
					transform: rotate(55deg);
					@media (--xl){
						top: -6px;
						left: -42px;
						transform: rotate(69deg);
					}
					@media (--md){
						width: 48px;
						top: 4px;
						left: -42px;
						transform: rotate(45deg);
					}
				}
			}
		}
	}
}