.stars{
	display: inline-block;
	@media (--lg){
		font-size: 0;
		display: block;
		margin-top: 24px;
		text-align: center;
	}
	@nest &__star{
		cursor: pointer;
		margin-right: 4px;
		display: inline-block;
		transition: background 300ms;
		background: resolve('diamond.png');
		width: width('diamond.png');
		height: height('diamond.png');
		@nest &.active{
			background: resolve('diamond-active.png');
			width: width('diamond-active.png');
			height: height('diamond-active.png');
		}
		@nest &:last-child{
			margin-right: 0;
		}
		@media (--xl){
			margin-right: 3px;
		}
		@media (--md){
			margin: 0 4px;
		}
	}
}