.filter-dropdown{
	position: absolute;
	content: '';
	left: 0;
	right: 0;
	top: 145%;
	height: 0;
	margin: 0;
	width: 165px;
	display: none;
	list-style: none;
	text-align: left;
	background: #fff;
	padding: 5px 10px 10px 30px;
	box-shadow: 5px 8.66px 10px 0px rgba(41, 80, 160, 0.2);
	@media (--lg){
		position: static;
		width: 100%;
		max-width: 165px;
		margin: 10px auto 15px;
	}
	@nest &--indent{
		padding: 5px 15px 20px;
	}
	@nest &.show{
		display: block;
		height: auto;
		z-index: 100;
	}
	@nest &__item{
		font-size: 0;
		margin-bottom: 10px;
		@nest &:last-child{
			margin-bottom: 0;
		}
	}
	@nest & input[type="checkbox"]{
		position: absolute;
		opacity: 0;
		transition: color 300ms;
		@nest &:checked +{
			@nest & label{
				color: #2557bf;
				@nest &:before{
					border: 0;
					background: #2557bf;
				}
				@nest &:after{
					opacity: 1;
				}
			}
		}
	}
	@nest & label{
		font-size: 12px;
		color: #010101;
		display: block;
		cursor: pointer;
		padding-left: 15px;
		position: relative;
		white-space: normal;
		line-height: normal;
		font-family: var(--font);
		@nest &:before{
			position: absolute;
			content: '';
			left: 0;
			top: 0;
			width: 12px;
			height: 12px;
			border-radius: 2px;
			border: 1px solid #7a7a7a;
			transition: background 300ms;
		}
		@nest &:after{
			position: absolute;
			content: '';
			top: 4px;
			left: 3px;
			opacity: 0;
			transition: opacity 300ms;
			background: resolve('inp-ok.svg');
			width: width('inp-ok.svg');
			height: height('inp-ok.svg');
		}
	}
	@nest & i{
		font-size: 10px;
		color: #010101;
		display: block;
		font-style: normal;
		margin-bottom: 5px;
		font-family: var(--font);
	}
	@nest &__price{
		font-size: 0;
		overflow: hidden;
		position: relative;
		border: 1px solid #9ab4e5;
		@nest &:after{
			position: absolute;
			content: '';
			top: 0;
			left: 50%;
			bottom: 0;
			width: 1px;
			height: 100%;
			margin-left: -.5px;
			background: #9ab4e5;
		}
		@nest & input{
			font-size: 14px;
			color: #2557bf;
			width: 50%;
			float: left;
			border: 0;
			padding: 0;
			height: 22px;
			outline: none;
			padding: 0 5px;
			text-align: center;
			font-family: var(--font);
			@nest &::placeholder{
				color: #2557bf;
			}
		}
	}
}