.overview{
	padding: 46px 0 20px;
	@media (--md){
		padding-top: 39px;
	}
	@nest &__inner{
		margin-top: 24px;
		position: relative;
		@media (--md){
			margin-top: 20px;
		}
		@nest & > img{
			position: absolute;
			left: -67px;
			top: -1px;
			@media (--xl){
				left: -91px;
			}
			@media (--lg){
				left: -315px;
			}
			@media (--md){
				position: static;
				margin: auto;
				display: block;
			}
		}
	}
	@nest &__block{
		clear: fix;
		max-width: 715px;
		margin-left: auto;
		border-radius: 13px;
		border: 1px solid #2950a0;
		padding: 43px 40px 31px 100px;
		@media (--xl){
			max-width: 537px;
			padding: 33px 10px 48px 80px;
		}
		@media (--md){
			margin-top: 24px;
			padding: 10px 25px 6px 53px;
		}
		@nest & h3{
			font-size: 18px;
			color: #2950a0;
			font-weight: 700;
			line-height: 22px;
			margin-bottom: 20px;
			font-family: var(--font);
			@media (--xl){
				margin-bottom: 25px;
			}
			@media (--md){
				margin-bottom: 13px;
			}
		}
	}
	@nest &__col{
		width: 100%;
		float: left;
		max-width: 255px;
		@media (--xl){
			max-width: 210px;
		}
		@media (--sm){
			float: none;
			max-width: 100%;
			padding-bottom: 1px;
		}
		@nest &--right{
			float: right;
			@media (--xl){
				max-width: 180px;
			}
			@media (--sm){
				float: none;
				max-width: 100%;
				margin-top: 5px;
			}
		}
	}
	@nest &__text{
		position: relative;
		margin-bottom: 25px;
		@media (--xl){
			margin-bottom: 16px;
		}
		@nest &:last-child{
			margin-bottom: 0;
			@media (--md){
				margin-bottom: 16px;
			}
		}
		@nest &--indent{
			margin-bottom: 18px;
			@media (--xl){
				margin-bottom: 34px;
			}
			@media (--md){
				margin: 20px 0;
			}
		}
		@nest &&--center{
			@media (--xl){
				margin-top: 20px;
			}
			@nest & img{
				top: 50%;
				transform: translateY(-50%);
			}
		}
		@nest & img{
			position: absolute;
			left: -38px;
			top: 4px;
			@media (--xl){
				left: -30px;
				top: -4px;
			}
			@media (--md){
				left: -32px;
				top: 2px;
			}
		}
		@nest & p{
			font-size: 12px;
			color: #282828;
			line-height: 18px;
			margin-bottom: 0;
			@media (--xl){
				font-size: 10px;
				line-height: inherit;
			}
		}
	}
}