.reviews{
	margin: 45px 0 0;
	@media (--xl){
		margin-top: 46px;
	}
	@media (--md){
		margin-top: 35px;
	}
	@nest &--indent{
		margin-top: 0;
		background: #e4eeff;
		padding: 25px 0 45px;
		@nest & .section-title{
			color: #2950a0;
		}
	}
	@nest &__inner{
		margin-top: 73px;
		@media (--md){
			margin-top: 71px;
		}
	}
	@nest & .section-title{
		@media (--sm){
			max-width: 150px;
		}
	}
	@nest &__more{
		margin-top: 14px;
		text-align: center;
		@nest & a{
			font-size: 14px;
			color: #000;
			line-height: normal;
			text-decoration: none;
			display: inline-block;
			font-family: var(--font);
			border-bottom: 1px solid #000;
			transition: border-bottom 300ms;
			@nest &:hover{
				border-bottom: 0;
			}
		}
	}
}