.btn-menu{
	position: absolute;
	content: '';
	top: 6px;
	right: 15px;
	border: 0;
	outline: none;
	width: 23px;
	height: 19px;
	padding: 0;
	z-index: 10;
	background: transparent;
	@media (--from-md){
		display: none;
	}
	@nest &__line{
		position: absolute;
		content: '';
		left: 0;
		right: 0;
		width: 100%;
		height: 3px;
		display: block;
		background: #1a49a0;
		transition: top 200ms, transform 200ms;
		@nest &:first-child{
			top: 0;
		}
		@nest &:nth-child(2){
			top: 50%;
			margin-top: -1.5px;
		}
		@nest &:last-child{
			bottom: 0;
		}
	}
	@nest &-open &{
		@nest &__line{
			@nest &:first-child{
				top: 50%;
				transform: translateY(-50%) rotate(45deg);
			}
			@nest &:nth-child(2){
				opacity: 0;
			}
			@nest &:nth-child(3){
				top: 50%;
				transform: translateY(-50%) rotate(-45deg);
			}
		}
	}
}