.modal{
	display: none;
	border-radius: 7px;
	background-color: rgb(255, 255, 255);
	box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.004);
	@nest &--city{
		width: 750px;
		padding: 35px 30px 25px;
		@media (--xl){
			width: 595px;
			padding-bottom: 1px;
			padding-left: 30px;
			padding-right: 30px;
		}
		@media (--md){
			width: auto;
		}
		@nest & span{
			font-size: 22px;
			color: #13387d;
			font-weight: 800;
			display: block;
			text-align: center;
			line-height: normal;
			@media (--xl){
				text-align: left;
			}
		}
		@nest & i{
			font-size: 16px;
			color: #13387d;
			font-weight: 500;
			display: block;
			font-style: normal;
			margin-bottom: 5px;
		}
		@nest & a{
			font-size: 16px;
			color: #7599e2;
			display: block;
			line-height: 24px;
			@nest &:hover{
				text-decoration: none;
			}
		}
	}
	@nest &--city &{
		@nest &__inner{
			margin-top: 30px;
		}
		@nest &__item{
			margin-bottom: 25px;
		}
	}
}

.fancybox-overlay{
	background: rgba(19, 56, 125,.3);
	background-image: none;
}
.fancybox-close{ 
	width: 16px; 
	height: 16px; 
	top: 28px; 
	right: 30px; 
	background-image: none; 
	background-color: #13387d; 
	-webkit-mask-image: url(../img/close.svg); 
	mask-image: url(../img/close.svg); 
	transition: background-color 300ms; 
} 
.fancybox-close:hover{ 
	background-color: #ee4040; 
}

.modal--call{
  width: 750px;
  text-align: center;
  padding-bottom: 30px;
}
.modal--call h2{
  font-size: 22px;
  font-weight: 800;
  margin-bottom: 30px;
  color: #13387d;
  padding-top: 35px;
}
.modal--call .order__contacts-item p{
  font-size: 16px;
  color: #2c2b2b;
  text-align: left;
  margin-bottom: 5px;
  font-weight: 400;
}
.modal--call form{
  width: 430px;
  margin: auto;
}
.modal--call form button{
  font-size: 18px;
  font-weight: 800;
  color: #13387d;
  border-style: solid;
  outline: none;
  border-width: 2px;
  border-color: rgb(154, 180, 229);
  border-radius: 6px;
  margin-bottom: 20px;
  background-color: rgb(255, 255, 255);
  width: 225px;
  height: 50px;
}
.modal--call form button:focus{
  border-style: solid;
  border-width: 1px;
  border-color: rgb(239, 168, 0);
  border-radius: 6px;
  background-color: #ffc700;
}
.modal--call  .order__contacts-item {
  margin-bottom: 25px;
  position: relative;
}
.modal--call .order__contacts-item input{
  width: 100%;
  border-radius: 4px;
  padding-left: 10px;
  border: 1px solid #e5e5e5;
  outline: none;
  background-color: rgb(255, 255, 255);
  height: 48px;
  box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.004),inset 0.469px 0.883px 2px 0px rgba(0, 0, 0, 0.1);
}
.modal--call  .order__contacts-item b{
  font-size: 16px;
  color: #ff0000;
  font-weight: 400;
}
.modal--call .order__contacts-item span{
  font-size: 12px;
  color: #ff0000;
  display: none;
  font-weight: 100;
  position: absolute;
  width: 130px;
  right: -130px;
  top: 50px;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  text-align: left;
  padding-left: 10px;
}