.where-table{
	margin: 23px 0 0;
	@nest &__title{
		font-size: 14px;
		color: #fff;
		font-weight: 800;
		padding: 5px 0 4px;
		text-align: center;
		line-height: normal;
		background: #13387d;
	}
	@nest & table{
		width: 100%;
		background: #e4eeff;
		border-radius: 0 0 17px 17px;
		@nest & thead{
			@nest & tr{
				border-bottom: 1px solid #9ab4e5;
				@nest & td{
					color: #1a49a0;
					font-weight: 700;
					padding: 4px 0;
				}
			}
		}
		@nest & tbody{
			@nest & tr{
				border-bottom: 1px solid #9ab4e5;
				@nest &:last-child{
					border-bottom: 0;
					@nest & td{
						padding-bottom: 18px;
					}
				}
			}
		}
		@nest & tr{
			@nest & td{
				font-size: 12px;
				color: #282828;
				padding: 14px 0 9px;
				font-family: var(--font);
				border-right: 1px solid #9ab4e5;
				@nest &:first-child{
					width: 34%;
					padding-left: 20px;
					@media (--lg){
						width: 50%;
					}
				}
				@nest &:nth-child(2){
					width: 38%;
					padding-left: 9px;
					@media (--lg){
						display: none;
					}
				}
				@nest &:last-child{
					width: 28%;
					border-right: 0;
					padding-left: 16px;
					@media (--lg){
						width: 50%;
					}
				}
				@nest & a{
					color: #282828;
					text-decoration: none;
					@nest &:hover{
						text-decoration: underline;
					}
				}
			}
		}
	}
}