.nav-list{
	margin: 0;
	padding: 0 20px;
	list-style: none;
	@media (--xl){
		padding: 0;
	}
	@media (--md){
		padding: 15px 0 20px;
	}
	@nest &__item{
		width: 20%;
		text-align: center;
		display: inline-block;
		@media (--xl){
			@nest &:first-child{
				text-align: left;
			}
			@nest &:last-child{
				text-align: right;
			}
		}
		@media (--md){
			display: block;
			width: 100%;
			margin-bottom: 5px;
		}
	}
	@nest &__link{
		font-size: 14px;
		color: #000;
		height: 25px;
		z-index: 10;
		padding: 0 27px;
		line-height: 25px;
		position: relative;
		text-align: center;
		display: inline-block;
		text-decoration: none;
		transition: color 300ms;
		@media (--xl){
			padding: 0;
			width: 70%;
		}
		@media (--lg){
			width: 100%;
		}
		@media (--md){
			color: #fff;
			height: auto;
			line-height: inherit;
		}
		@nest &:after{
			position: absolute;
			content: '';
			top: -1px;
			bottom: -1px;
			left: 0;
			right: 0;
			height: 0;
			z-index: -1;
			background: #13387d;
			border-radius: 0 0 6px 6px;
			transition: height 200ms;
			@media (--md){
				display: none;
			}
		}
		@nest &:hover{
			color: #fff;
			@media (--md){
				text-decoration: underline;
			}
			@nest &:after{
				height: 100%;
			}
		}
	}
}