.footer{
	background: #feeeb6;
	padding: 18px 0 31px;
	@media (--lg){
		padding: 21px 0 22px;
	}
	@nest &__inner{
		@media (--lg){
			max-width: 300px;
			margin: auto;
		}
	}
	@nest & span{
		font-size: 12px;
		color: #000;
		font-weight: 700;
		line-height: 14px;
		margin-bottom: 14px;
		display: inline-block;
		@media (--lg){
			display: none;
			margin-bottom: 8px;
		}
	}
	@nest & ul{
		padding: 0;
		margin: 0;
		list-style: none;
		@nest & a{
			font-size: 12px;
			color: #000;
			display: block;
			line-height: 14px;
			@nest &:hover{
				text-decoration: none;
			}
			@media (--lg){
				line-height: 18px;
			}
		}
	}
	@nest &__center{
		max-width: 293px;
		@media (--lg){
			max-width: 100%;
			text-align: center;
			margin-top: 13px;
		}
		@nest & span{
			@media (--lg){
				display: block;
				margin-bottom: 0;
			}
		}
	}
	@nest &__social{
		float: right;
		font-size: 0;
		margin-top: 6px;
		@media (--lg){
			float: none;
		}
		@nest & a{
			margin-left: 11.5px;
			display: inline-block;
			text-decoration: none;
			@media (--lg){
				margin: 0 6px;
			}
		}
	}
	@nest & p{
		font-size: 10px;
		color: #000;
		margin-top: 10px;
		line-height: 12px;
		font-family: var(--font);
		@media (--lg){
			display: none;
		}
	}
	@nest &__number{
		font-size: 24px;
		color: #000;
		display: block;
		margin: 3px 0 15px;
		line-height: normal;
		text-decoration: none;
		font-family: var(--font);
		@media (--xl){
			font-size: 18px;
			margin: -3px 0 28px;
		}
		@media (--lg){
			margin-bottom: 9px;
		}
	}
	@nest &__btn-modal{
		font-size: 12px;
		color: #000;
		z-index: 10;
		display: block;
		outline: none;
		max-width: 125px;
		margin-left: auto;
		margin-top: 30px;
		padding: 0 10px;
		min-height: 27px;
		line-height: 25px;
		font-weight: 500;
		overflow: hidden;
		border-radius: 5px;
		text-align: center;
		position: relative;
		background: #ffc700;
		text-decoration: none;
		transition: color 300ms;
		border: 1px solid #a4a2ce;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: 0;
			height: 0;
			z-index: -1;
			background: #f51610;
			transition: height 300ms;
		}
		@nest &:hover, &:focus{
			color: #fff;
			border: 1px solid #fff;
			@nest &:after{
				height: 100%;
			}
		}
		@media (--lg){
			float: right;
			margin: 5px 5px 0 0;
		}
	}
	@nest &__item{
		float: left;
		@media (--lg){
			float: none;
			clear: fix;
			margin-top: 15px;
		}
		@nest & span{
			@media (--lg){
				display: block;
				font-size: 14px;
			}
		}
	}
	@nest &__wrap{
		@media (--lg){
			float: left;
		}
	}
	@nest &__right{
		float: right;
		margin-top: 5px;
		@media (--lg){
			display: none;
		}
		@nest & i{
			color: #1a49a0;
			font-size: 12px;
			display: block;
			font-style: normal;
			line-height: 14px;
			@nest & a{
				margin: 0;
				font-size: 12px;
				position: relative;
				display: inline-block;
				text-decoration: underline;
				@nest &:hover{
					text-decoration: none;
				}
				@media (--xl){
					font-size: 10px;
				}
				@nest & img{
					position: absolute;
					content: '';
					top: 0;
					right: -22px;
				}
			}
		}
		@nest & a{
			font-size: 14px;
			color: #1a49a0;
			display: block;
			margin-top: 12px;
			text-decoration: none;
		}
	}
	@nest &__right-mobile{
		text-align: center;
		@media (--from-lg){
			display: none;
		}
		@nest & i{
			font-size: 10px;
			color: #1a49a0;
			display: block;
			font-style: normal;
			@nest & a{
				color: #1a49a0;
				@nest &:last-child{
					font-size: 14px;
					display: block;
					text-decoration: none;
					@nest & img{
						display: inline-block;
						vertical-align: middle;
					}
				}
			}
		}
	}
}