.about{
	margin: 46px 0 0;
	@media (--md){
		margin-top: 37px;
	}
	@nest &__inner{
		margin: 24px 0 0;
		@media (--md){
			margin-top: 22px;
		}
	}
	@nest &__text{
		border-radius: 14px;
		border: 1px solid #2557bf;
		padding: 20px 20px 18px 22px;
		@nest & p{
			font-size: 14px;
			color: #000;
			line-height: 18px;
			margin-bottom: 20px;
			@nest &:last-child{
				margin-bottom: 0;
			}
			@media (--xl){
				font-size: 12px;
			}
			@media (--md){
				font-size: 10px;
			}
		}
	}
}